import { PageChangeEvent, GridDataResult } from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs';

export abstract class GridBaseComponent<T> {
    private _searchValue: string;

    public pageSize = 15;
    public skip = 0;
    public gridView: GridDataResult;
    public data: T[];
    public get searchValue(): string {
        return this._searchValue;
    }
    public set searchValue(value) {
        this._searchValue = value;
        this.loadGridView();
    }


    // TODO:
    // constructor(private observableData: Observable<T>) {
    //     observableData.subscribe((data: T[]) => {
    //         this.loadData(data)
    //     });
    // }

    protected loadData(data: T[]) {
        this.data = data;
        this.loadGridView();
    }

    protected pageChange({ skip, take }: PageChangeEvent): void {
        this.skip = skip;
        this.pageSize = take;
        this.loadGridView();
    }

    protected loadGridView(): void {
        let filteredData = this.applyPagination(this.applySearch());

        this.gridView = {
            data: filteredData,
            total: this.data.length
        };
    }

    protected applySearch(): T[] {
        if (!this.searchValue) {
            return this.data;
        }

        return this.data.filter((item) => {
            return this.checkIfItemContainsText(item, this.searchValue);
        });
    }

    private applyPagination(data: T[]) {
        return data.slice(this.skip, this.skip + this.pageSize);
    }

    private checkIfItemContainsText(item: T, text: string) {
        let itemFound = false;
        Object.getOwnPropertyNames(item).forEach(prop => {
            if (item[prop].toString().toUpperCase().indexOf(text.toUpperCase()) > -1) {
                itemFound = true;
                // TODO: Exit from loop
            }
        })
        return itemFound;
    }
}