import { Component, OnInit } from '@angular/core';
import { DialogService } from '@progress/kendo-angular-dialog';

import { MeasuredData } from './models/measured-data.model';
import { GridBaseComponent } from 'packages/@g-a-s-shared/shared/src/app/grid/grid-base.component';
import { DetailsComponent } from './components/details/details.component';
import { InfoComponent } from './components/info/info.component';

@Component({
  selector: 'app-control-panel',
  templateUrl: './control-panel.component.html',
  styleUrls: ['./control-panel.component.scss']
})
export class ControlPanelComponent extends GridBaseComponent<MeasuredData> implements OnInit {

  public fakeDataItem = new MeasuredData();

  public fakeData: MeasuredData[] = [
    {
      date: '11/29/2019 -- 15:00',
      temperature: 25,
      concentration: 1.2,
      humidity: 60,
      type: 1,
      name: 'CO',
      id: 1
    },
    {
      date: '11/29/2019 -- 23:00',
      temperature: 21,
      concentration: 7.2,
      humidity: 50,
      type: 1,
      name: 'CO',
      id: 2
    }
  ];

  public columns = [
    {
      field: 'date',
      title: 'Дата',
      columnMenu: true
    }, {
      field: 'temperature',
      title: 'Температура',
      columnMenu: true
    }, {
      field: 'concentration',
      title: 'Концентрація',
      columnMenu: true
    }, {
      field: 'name',
      title: 'Назва сполуки',
      columnMenu: true
    }, {
      button: 'details',
      title: '',
      columnMenu: false
    }
  ];

  constructor(private dialogService: DialogService) {
    super();
  }

  ngOnInit() {
    this.loadData(this.fakeData);
  }

  public showHistory(item: any) {
    this.fakeDataItem = item;
    this.dialogService.open({ content: DetailsComponent });
  }

  public showInfo() {
    this.dialogService.open({ content: InfoComponent });
  }
}
