export class MeasuredData  {
    date: string;
    temperature: number;
    concentration: number;
    humidity: number;
    type: GasType;
    id: number;
    name: string;
}
export enum GasType {
    co = 1,
    co2 = 2,
    no2 = 3,
    ch2o = 4,
    h2s = 5
}

export class MeasuredDataFormErrorsViewModel {
    date: string = null;
    temperature: number = null;
    concentration: number = null;
    humidity: number = null;
    type: any = null;
    name: string = null;
}